<template>
  <div class="chart">
    <div class="name">图表</div>
    <div>
      <el-select v-model="jobType" @change="selectType" placeholder="请选择职业名称">
        <el-option
            v-for="item in wantJobArr"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="charts">
      <div>
        <div ref="education"></div>
      </div>
      <div>
        <div ref="city"></div>
      </div>
    </div>
<!--    <div class="footCharts">-->
    <div class="charts">
      <div>
        <div ref="educationCity"></div>
<!--        <div ref="wordCloud"></div>-->
      </div>
      <div>
        <div ref="wordCloud"></div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
require('echarts-wordcloud')
export default {
  name: 'chart',
  data() {
    return {
      jobType: '',
      wantJobArr: [
        // {label: '销售/商务拓展', value: 19000000000000},
        // {label: '人事/行政/财务/法务', value: 14000000000000},
        // {label: '互联网/通信及硬件', value: 9000000000000},
        // {label: '运维/测试', value: 20000000000000},
        // {label: '视觉/交互/设计', value: 17000000000000},
        // {label: '运营/专业分析', value: 5000000000000},
        // {label: '产品/项目/高级管理', value: 3000000000000},
        // {label: '市场/品牌/公关', value: 16000000000000},
        // {label: '金融/保险', value: 12000000000000},
        // {label: '房地产/工程/建筑', value: 7000000000000},
        // {label: '物流/采购/供应链', value: 2000000000000},
        // {label: '生产制造/营运管理', value: 15000000000000},
        // {label: '农业/能源/环保', value: 21000000000000},
        // {label: '医疗/医美', value: 18000000000000},
        // {label: '教育/培训/科研', value: 11000000000000},
        // {label: '编辑/记者/翻译', value: 1000000000000},
        // {label: '影视传媒', value: 4000000000000},
        // {label: '商务服务/生活服务', value: 6000000000000},
        // {label: '管培生/非企业从业者', value: 8000000000000}
        {label: 'java', value: 'java'},
        {label: '前端', value: '前端'},
        {label: '测试', value: '测试'},
        {label: '大数据', value: '大数据'},
        {label: '算法', value: '算法'},
        {label: '嵌入式', value: '嵌入式'},
        {label: '运维', value: '运维'},
        {label: '数据分析', value: '数据分析'},
        {label: '产品', value: '产品'}
      ],
      education: ['高中', '大专', '本科', '硕士', '博士']
    }
  },
  mounted() {
    // this.selectType()
    // this.getWordCloud()
  },
  methods: {
    getWordCloud() {
      let maskImage = new Image()
      maskImage.src = 'data:image/png;base64,'
      // http://127.0.0.1:5000/api/data/ciyun
      let wordCloud = this.$refs.wordCloud
      let word = this.$echarts.init(wordCloud)
      this.$http.get('api/data/ciyun', {
        params: {
          job: this.jobType
        }
      }).then(res => {
        if (res.data.code === 200) {
          let data = res.data.data

          let option = {
            tooltip: {
              show: true,
              transitionDuration: 0
            },
            series: [
              {
                type: 'wordCloud',
                textPadding: 0,
                sizeRange: [12, 55],
                // width: '98%',
                // height: '98%',
                autoSize: {
                  enable: true,
                  minSize: 6
                },
                left: 'center',
                top: 'center',
                right: null,
                bottom: null,
                data: data,
                maskImage: maskImage,
                textStyle: {
                  normal: {
                    color: function () {
                      return 'rgb(' + [
                        Math.round(Math.random() * 105) + 120,
                        Math.round(Math.random() * 105) + 120,
                        Math.round(Math.random() * 105) + 120
                      ].join(',') + ')'
                    }
                  },
                  emphasis: {
                    shadowBlur: 10,
                    shadowColor: '#e8e8e8'
                  }
                }
              }
            ]
          }
          word.setOption(option)
        }
      })
    },
    selectType() {
      this.getEducation()
      this.getCity()
      this.getEducationCity()
      this.getWordCloud()
    },
    getEducation() {
      let education = this.$refs.education
      let bar = this.$echarts.init(education)
      this.$http.get('api/data/job/education', {
        params: {
          job: this.jobType
        }
      }).then(res => {
        let [x, y] = [[], []]
        if (res.data.code === 200) {
          let obj = res.data.data
          delete obj['初中及以下']
          let b = []
          for (let i in obj) {
            this.education.forEach((a, j) => {
              if (a === i) {
                let c = {i: j, name: i, value: obj[i]}
                b.push(c)
              }
            })
          }
          b.sort((q, e) => {
            return q.i - e.i
          })
          b.forEach(a => {
            x.push(a.name)
            y.push(a.value)
          })
          let series = []
          for (let i = 0; i < x.length; i++) {
            let obj = {
              label: {
                align: 'center'
              },
              name: x[i],
              type: 'bar',
              data: [y[i]]
            }
            series.push(obj)
          }
          let option = {
            grid: {
              bottom: '10%',
              right: '36px',
              left: '56px'
            },
            // color: ['#5470c6'],
            legend: {
              top: '1%'
            },
            color: ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
            title: {
              text: '不同学历薪资',
              left: 'center',
              top: '8%'
            },
            tooltip: {
              formatter: function (params) {
                // let str = '<span style="display:inline-block;width:10px;height:10px;background:' + params.color + ';border-radius: 50%;margin-right:5px;"></span>' + params.name + '：' + params.value + '万'
                // return str
                let xValue = params[0].axisValue
                let levelValues = ''
                for (let i = 0; i < params.length; i++) {
                  if (params[i].value !== 0) {
                    levelValues += '<span style="display:inline-block;width:10px;height:10px;background:' + params[i].color + ';border-radius: 50%;margin-right:5px;"></span>' + params[i].seriesName + '：' + params[i].value + '万<br>'
                  }
                }
                return xValue + '<br>' + levelValues
              },
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              },
              transitionDuration: 0
            },
            xAxis: {
              data: ['学历']
              // data: x
            },
            yAxis: {
              type: 'value',
              name: '万'
            },
            series: series
            // series: [
            //   {
            //     label: {
            //       align: 'center'
            //     },
            //     type: 'bar',
            //     // itemStyle: {
            //     //   normal: {
            //     //     // 这里就可以实现，配置柱状图的颜色
            //     //     color: function (params) {
            //     //       let colorList = ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc']
            //     //       return colorList[params.dataIndex]
            //     //     },
            //     //   }
            //     // },
            //     data: y,
            //     backgroundStyle: {
            //       color: 'rgba(180, 180, 180, 0.2)'
            //     }
            //   }
            // ]
          }
          bar.setOption(option)
        }
      })
    },
    getCity() {
      let city = this.$refs.city
      let pie = this.$echarts.init(city)
      this.$http.get('api/data/job/city', {
        params: {
          job: this.jobType
        }
      }).then(res => {
        if (res.data.code === 200) {
          let obj = res.data.data
          let arr = []
          for (let i in obj) {
            let a = {value: obj[i], name: i}
            arr.push(a)
          }
          let option = {
            legend: {
              top: '1%'
            },
            grid: {
              bottom: '10%'
            },
            color: ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
            tooltip: {
              trigger: 'item',
              formatter: function (params) {
                let str = '<span style="display:inline-block;width:10px;height:10px;background:' + params.color + ';border-radius: 50%;margin-right:5px;"></span>' + params.name + '：' + params.value + '万'
                return str
              },
              transitionDuration: 0
            },
            title: {
              text: '不同城市薪资',
              left: 'center',
              top: '8%'
            },
            series: [
              {
                type: 'pie',
                radius: '60%',
                bottom: '10px',
                center: ['50%', '60%'],
                data: arr,
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          }
          pie.setOption(option)
        }
      })
      // 不同城市薪资
    },
    getEducationCity() {
      // 不同城市学历要求
      let educationCity = this.$refs.educationCity
      let bar = this.$echarts.init(educationCity)
      this.$http.get('api/data/job/city/education', {
        params: {
          job: this.jobType
        }
      }).then(res => {
        let [x, y, name] = [[], [], []]
        if (res.data.code === 200) {
          let obj = res.data.data
          let b = []
          for (let i in obj) {
            x.push(i)
            let a = []
            delete obj[i]['初中及以下']
            // let c = {}
            for (let j in obj[i]) {
              // this.education.forEach((a, k) => {
              //   if (a === j) {
              //     c = {i: k, name: j}
              //   }
              // })
              name.push(j)
              a.push(obj[i][j])
              // c.value = a
              // b.push(c)
            }
            y.push(a)
          }
          // b.sort((q, e) => {
          //   return q.i - e.i
          // })
          // console.log(b)
          // let object = {}
          // b = b.reduce(function(a, c) {
          //   object[c.name] ? '' : object[c.name] = true && a.push(c)
          //   return a
          // }, [])
          // console.log(b)
          // name = b.map(a => a.name)
          // y = b.map(a => a.value)
          name = [...new Set(name)]
          let series = []
          for (let i = 0; i < x.length; i++) {
            let obj = {
              label: {
                align: 'center'
              },
              name: x[i],
              type: 'bar',
              data: y[i]
            }
            series.push(obj)
          }
          console.log(series)
          let option = {
            legend: {
              top: '1%'
            },
            color: ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
            grid: {
              bottom: '10%',
              right: '36px',
              left: '56px'
            },
            title: {
              text: '不同城市学历要求',
              left: 'center',
              top: '8%'
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              },
              transitionDuration: 0,
              formatter: function (params) {
                let xValue = params[0].axisValue
                let levelValues = ''
                for (let i = 0; i < params.length; i++) {
                  if (params[i].value !== 0) {
                    levelValues += '<span style="display:inline-block;width:10px;height:10px;background:' + params[i].color + ';border-radius: 50%;margin-right:5px;"></span>' + params[i].seriesName + '：' + params[i].value + '万<br>'
                  }
                }
                return xValue + '<br>' + levelValues
              }
            },
            xAxis: {
              data: name
            },
            yAxis: {
              type: 'value',
              name: '万'
            },
            series: series
          }
          bar.setOption(option)
        }
      })
    },
  }
}
</script>

<style scoped src="./chart.less" lang="less"></style>
